
const initState = {
    signInStatus: '',
    signIn: '',
    accountDetails: null
};

const reducer = (state = initState, action) => {
    const { type, payload } = action;
    switch (type) {
        case "LOGIN": {
            const { accountDetails } = payload;
            
            const nextState = {
                signInStatus: '',
                signIn: '',
                accountDetails: accountDetails
            }
            return {...state, ...nextState};
        }

        case "LOGOUT": {
            const nextState = {
                signInStatus: '',
                signIn: '',
                accountDetails: null
            }
            return {...state, ...nextState};
        }

        default: {
            return state;
        }
    }
}
export default reducer;